import { createEvent } from 'effector';
export function createTriggers(name) {
    const onValidate = createEvent(`${name}ValidationTriggered`);
    const onForceValidate = createEvent(`${name}ForceValidationTriggered`);
    const onReset = createEvent(`${name}Reset`);
    const onValueSet = createEvent(`${name}SetValue`);
    return {
        onValidate,
        onForceValidate,
        onReset,
        onValueSet,
    };
}
export function createEvents(name) {
    const onChanged = createEvent(`${name}Changed`);
    const onFocused = createEvent(`${name}Focused`);
    const onBlurred = createEvent(`${name}Blurred`);
    const onTouched = createEvent(`${name}Touched`);
    return {
        onChanged,
        onFocused,
        onBlurred,
        onTouched,
    };
}
export function getFieldSetValueAsArray(fields) {
    return fields.map((field) => field.value);
}
export function getFieldSetValueAsObject(fields) {
    const values = {};
    fields.forEach((field) => {
        values[field.name] = field.value;
    });
    return values;
}
