import { createStore, sample } from 'effector';
import { createField } from '../createField';
export const createCheckbox = ({ name, initialValue = '', isRequired, defaultChecked = false, isBoolean, requiredErrorText = 'ÐŸÐ¾Ð»Ðµ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ð´Ð»Ñ Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ', }) => {
    const getInitialValue = () => {
        if (defaultChecked) {
            if (isBoolean)
                return true;
            return initialValue;
        }
        return isBoolean ? false : '';
    };
    const $value = createStore(getInitialValue());
    const field = createField({ name, initialValue: getInitialValue(), validateOn: 'change' });
    $value.on(field.handlers.onChange, (_, e) => {
        if (isBoolean)
            return e.currentTarget.checked;
        return e.currentTarget.checked ? initialValue : '';
    });
    sample({
        source: [$value, field.isTouched],
        clock: field.triggers.forceValidate,
        fn: ([value, isTouched]) => {
            if (isTouched && isRequired && !value)
                return requiredErrorText;
            return null;
        },
        target: field.error,
    });
    return {
        value: $value,
        ...field,
    };
};
