import * as React from 'react';
const SelectOption = ({ value, label, selected, disabled }) => (<option value={value} selected={selected} disabled={disabled}>
    {label}
  </option>);
const SelectOptions = ({ options }) => (<>
    {options.map((option) => (<SelectOption key={option.value} value={option.value} label={option.label} disabled={option.disabled} {...option}/>))}
  </>);
export const Select = ({ name, onBlur, onFocus, onChange, defaultValue, isDisabled, options, ...props }) => (<select name={name} onBlur={onBlur} onFocus={onFocus} onChange={onChange} defaultValue={defaultValue} disabled={isDisabled} {...props}>
    <SelectOptions options={options}/>
  </select>);
