import { createStore, sample } from 'effector';
import { createField } from '../createField';
export const createInput = ({ name, initialValue = '', isRequired = false, requiredErrorText = 'ÐŸÐ¾Ð»Ðµ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ð´Ð»Ñ Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ', validateOn, validators = [], }) => {
    const field = createField({ name, initialValue, validateOn });
    const $value = createStore(initialValue);
    $value.on(field.handlers.onChange, (_, e) => e.currentTarget.value).reset(field.triggers.reset);
    sample({
        source: [$value, field.isTouched],
        clock: field.triggers.forceValidate,
        fn: ([value, isTouched]) => {
            if (isTouched && isRequired && `${value}`.length === 0)
                return requiredErrorText;
            if (isTouched && Boolean(validators.length)) {
                for (const element of validators) {
                    if (element(value))
                        return element(value);
                }
                return null;
            }
            return null;
        },
        target: field.error,
    });
    sample({ source: $value, clock: field.handlers.onBlur });
    return {
        value: $value,
        ...field,
    };
};
